import React, { Component } from "react";
import PropTypes from "prop-types";
import marked from "marked";
import style from "../css/ProMd.css";

class ProMd extends Component {
  getLanguages() {
    fetch(
      "https://raw.githubusercontent.com/emajidev/" +
        this.props.name +
        "/master/README.md"
    )
      .then((response) => response.text())
      .then((result) => {
        this.refs.test.innerHTML = marked(result);
        console.log(result);
      });
  }

  componentDidMount() {
    this.getLanguages();
    console.log("promd");
  }
  constructor(props) {
    super(props);
  }

  render() {
    return <div className="inner-container" ref="test"></div>;
  }
}

export default ProMd;
